.navigation {
    width: 100vw;
    background-color: #2196F3;
    color: #ffffff;
    display: block;
}

.navigation a {
    color: #ffffff;
    padding: 1em 2em;
    display: inline-block;
    text-decoration: none;
}

.navigation a:hover {
    background-color: #1565C0;
}

.card {
    width: 80vw;
    text-align: center;
    margin: 0 auto;
    padding: .5em;
    box-shadow: 0 2px 10px 1px #BDBDBD;
    border-radius: 4px;
    margin-bottom: 1em;
}

.card button {
    background-color: transparent;
    border: none;
    margin: 1em;
    outline: none;
}

.card button a {
    border: none;
    background-color: #4CAF50;
    color: #ffffff;
    padding: 1em 4em;
    outline: none;
    border-radius: 4px;
    text-decoration: none;
}

.card button a:hover {
    background-color: #2E7D32;
    cursor: pointer;
}
footer {
    width: 100vw;
    margin: 0;
    padding: 1em;
    background-color: #37474F;
    color: #ffffff;
    text-align: center;
}

footer nav a {
    color: #ffffff;
    text-decoration: none;
    padding: 0.5em;
}

footer nav a:hover {
    text-decoration: underline;
}
html, body {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, Sans-serif;
    color: #424242;
    background-color: #FAFAFA;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

main h1 {
    text-align: center;
}
